<template>
    <div class="html">
        <div class="licence">
            <h2>公司证照</h2>
            <ul class="imgtop1">
                <li class="imgtop" @click="block(index)" v-for="(item,index) in img" :key="index">
                    <img class="img" :src="item.picture_url" alt>
                    <p>{{item.describe}}</p>
                </li>
            </ul>
            <transition name="el-fade-in-linear">
                <div class="showimg" @click="imgshow=!imgshow" v-show="imgshow">
                    <div class="showimginner">
                        <img :src="showimg" alt>
                        <img @click="bigimg()" src="@/assets/about/big_img.png" alt>
                    </div>
                </div>
                
            </transition>
			<!-- 放大的图 -->
			<img v-show="bigshow" @click="bigshow=!bigshow" :src="showimg" alt class="bigshow">
        </div>
    </div>
</template>

<script>
import reuqest from "@/utils/request";
export default {
    components: {},
    name: "HelloWorld",
    data() {
        return {
			img: ""
			// [
            //     { img: require("@/assets/about/2.jpg"), t: "证照1" },
            //     { img: require("@/assets/about/2.jpg"), t: "222" },
            //     { img: require("@/assets/about/2.jpg"), t: "证照1" },
            //     { img: require("@/assets/about/2.jpg"), t: "证照1" },
            //     { img: require("@/assets/about/2.jpg"), t: "333" },
            //     { img: require("@/assets/about/2.jpg"), t: "证照1" }
			// ]
			,
            // 是否显示
            imgshow: false,
            // 大图
            bigshow: false,
            // 暂存图片
            showimg: ""
        };
    },
    created() {
        // 获取营业执照
        reuqest({
        	url: '/api/getConpanyPicture',
        	method: "post",
        }).then(data=>{
			console.log(data.data)
			if(data.status==200){
				this.img = data.data.data
			}
        })
    },
    methods: {
        // 显示当前img
        block(index) {
            this.imgshow = true;
			this.showimg = this.img[index].picture_url;
			console.log(this.showimg)
        },
        // 放大显示
        bigimg() {
			this.bigshow = true;
			
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.html {
    background: #e6e6e6;
}
.licence {
    display: inline-block;
    width: 1200px;
    background: white;
    margin: 0 auto;
    padding: 0 50px 20px;
}
h2 {
    font-size: 20px;
    padding-bottom: 10px;
    color: #333;
    border-bottom: 1px solid #999;
    margin-bottom: 20px;
    margin-top: 18px;
    text-align: left;
}
.img {
	display: inline-block;
    width: 202px;
    height: 263px;
    border: 1px solid #d6d6d6;
}
.img:hover {
    border: 1px solid red;
}
.imgtop {
    vertical-align: top;
    /* float: left; */
    margin-right: 22px;
    cursor: pointer;
}
.imgtop:nth-of-type(5) {
    margin-right: 0;
}
.imgtop1 {
    overflow: hidden;
    text-align: left;
}
.imgtop1 p {
    text-align: center;
    margin-bottom: 15px;
    line-height: 36px;
    font-size: 14px;
    color: #333;
    font-weight: 600;
}
.showimg {
    transition: 1s;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.527);
}
.showimginner {
    width: 700px;
    height: 520px;
    background: #fff;
    position: relative;
    margin: 10% auto;
    /* transform: translate(-50%, -50%); */
    border-radius: 20px;
}
.showimginner img {
    margin-top: 21px;
    width: 658px;
    height: 477px;
}
.showimginner img:nth-of-type(2) {
	cursor: pointer;
    width: 24px;
    height: 24px;
    position: absolute;
    right: 30px;
    top: 10px;
}
/* 大图 */
.bigshow{
	position: fixed;
	left: 0;
	top: 0;
	z-index: 99;
	display: inline-block;
	width: 100%;
	height: 100%;

}
</style>



